<template>
  <div class="shopcart">
    <div class="nav"></div>
    <div class="search">
      <div class="warp">
        <img src="../assets/img/3jdlogo.png" />
        <div class="search_div">
          <input type="text" class="search_text" />
          <input type="button" value="搜索" class="search_but" />
        </div>
      </div>
    </div>
    <div class="title warp">
      <h3>全部商品</h3>
    </div>
    <div class="tips warp">
      <ul>
        <li><input type="checkbox" v-model="isAllChecked" />全选</li>
        <li>商品</li>
        <li>单价</li>
        <li>数量</li>
        <li>小计</li>
        <li>操作</li>
      </ul>
    </div>
    <!--商品详情展示开始-->
    <div class="info warp" v-for="(g, index) in goods2" :key="index">
      <ul>
        <li class="info_1">
          <input type="checkbox" v-model="goods2[index].done" />
        </li>
        <li class="info_2"><img :src="getPicture(index)" width="80px" /></li>
        <li class="info_3">
          <a>{{ g.title}}</a>
        </li>
        <li class="info_4">
          <a>{{ g.version }}</a>
        </li>
        <li class="info_5">￥{{ g.price }}</li>
        <li class="info_6">
          <button @click="jian(index)">-</button>
          <input type="text" name="" id="" :value="g.num" />
          <button class="bot" @click="jia(index)">+</button>
        </li>
        <li class="info_7">￥{{ sSum(index) }}</li>
        <li class="info_8">
          <a href="javascript:;" @click="handleDelete(index)">删除</a><br />
          <a>已到我的关注</a>
        </li>
      </ul>
    </div>

    <div class="balance warp">
      <ul class="balance_ul1">
        <li><input type="checkbox" v-model="isAllChecked" />全选</li>
        <li @click="deleteGood" class="deldetegood">删除选中商品</li>
        <li>移到我的关注</li>
        <li>清除下柜商品</li>
      </ul>

      <ul class="balance_ul2">
        <li>
          已经选择<span>{{ some }}</span
          >件商品
        </li>
        <li>
          总价<span>￥{{ sum }}</span>
        </li>
        <li class="butt">去结算</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "Shopcart",
  data() {
    return {
      goods2: null,
    };
  },
  computed: {
    some() {
      return this.goods2.reduce((pre, g) => pre + (g.done ? 1 : 0), 0);
    },
    sum() {
      let s = 0;
      this.goods2.forEach((el) => {
        if (el.done == 1) {
          s += el.num * el.price;
        }
      });
      return s;
    },
    isAllChecked: {
      get() {
        return this.some === this.goods2.length && this.goods2.length > 0;
      },
      set(val) {
        val = val === true?1:0;
        this.allChecked(val);
      },
    },
  },
  watch: {
    goods2: {
      deep: true,
      handler(newval) {
        if (this.goods2 !== null) {
          localStorage.setItem("goods2", JSON.stringify(newval));
        }
      },
    },
  },
  methods: {
    jian(index) {
      if (this.goods2[index].num > 1) this.goods2[index].num--;
    },
    jia(index) {
      this.goods2[index].num++;
    },
    sSum(index) {
      return this.goods2[index].price * this.goods2[index].num;
    },
    checkChange(index) {
      this.goods2[index].done = !this.goods2[index].done;
    },
    allChecked(val) {
      this.goods2.forEach((el) => {
        el.done = val;
      });
    },
    deleteGood() {
      this.goods2 = this.goods2.filter((el) => el.done == 0);
    },
    handleDelete(index) {
      this.goods2.splice(index, 1);
    },
    getGoods2() {
      return this.$route.params.good;
    },
    getPicture(index) {
      let reg = /img\/(.*).jpg$/;
      let res = [];
      this.goods2.forEach((el) => {
        let str = el.url.src;
        res.push(str.match(reg)[1]);
        // console.log('遍历goods2取出图片名称',res);
      });
      return require("../assets/img/" + res[index] + ".jpg");
    },
  },
  created() {
    if (!localStorage.getItem("goods2")) {
        this.goods2 = [this.getGoods2()];
      // console.log("进入没有本地存储", this.goods2);
    } else {
      //判断是不是从路由跳转的
      if (!this.getGoods2()) {
        this.goods2 = JSON.parse(localStorage.getItem("goods2"));
        // console.log("进入有本地存储且 不是路由跳转或路由没有传递数据", this.goods2);
      } else {
        //判断本地存储是否为同一个
        //遍历本地存储中的goods2判断其id如果有相同的就让他的num与路由传过来的num相加
        //如果没有相同的就加入goods2数组
        // console.log("进入有本地存储且是路由跳转(初)", this.goods2);
        let id1 = this.getGoods2().id;
        this.goods2 = JSON.parse(localStorage.getItem("goods2"));
        this.goods2.forEach(el=>{
          if(el.id == id1){
              el.num += this.getGoods2().num
          }
        });
        if(!this.goods2.some(el=>el.id == id1)){
          this.goods2.push(this.getGoods2());
          // console.log('本地存储中不存在相同商品',this.goods2);
        }
      }
    }
  },
};
</script>

<style lang="less" scoped>
