  float: right;
  margin-top: 20px;
}
.search_text {
  width: 265px;
  height: 21px;
  border: 3px solid #c91623;
  position: relative;
  left: 4px;
  top: -1px;
}
.search_but {
  width: 51px;
  height: 29px;
  background-color: #c91623;
  border: 0px;
  color: #ffffff;
}
/*搜索框结束*/
/*标题开始*/
.title {
  margin-top: 130px;
  h3 {
    float: left;
    font-size: 23px;
    color: #c91623;
  }
}
/*标题结束*/
/*显示菜单的开始*/
.tips {
  width: 1000px;
  height: 50px;
  background-color: #f1f1f1;
  margin-top: 165px;
  border: 1px solid #e9e9e9;
  li {
    float: left;
    line-height: 50px;
    font-size: 12px;
    color: gray;
    &:nth-child(1) {
      width: 90px;
      margin-left: 10px;
      input {
        margin-right: 10px;
      }
    }
    &:nth-child(2) {
      margin-left: 70px;
    }
    &:nth-child(3) {
      margin-left: 430px;
    }
    &:nth-child(4) {
      margin-left: 70px;
    }
    &:nth-child(5) {
      margin-left: 85px;
    }
    &:nth-child(6) {
      margin-left: 65px;
    }
  }
}

/*显示菜单的结束*/
/*商品详情展示开始*/
.info {
  width: 1000px;
  height: 125px;
  background-color: #fff4e8;
  border: 1px solid gray;
  margin-top: 30px;
  border-top: 3px solid gray;
  position: relative;
  li {
    float: left;
    margin-top: 20px;
    position: absolute;
  }
  a {
    font-size: 12px;
    color: #333333;
  }
}
.info_1 {
  left: 10px;
}
.info_2 {
  border: 1px solid gray;
  left: 35px;
}
.info_3 {
  width: 270px;
  height: 20px;
  left: 120px;
}
.info_4 {
  width: 140px;
  left: 420px;
}
.info_5 {
  left: 607px;
  font-size: 16px;
}
.info_6 {
  left: 695px;
  input {
    width: 30px;
